#contain-landing {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;    
    text-align: center;
}


:root {
    --grow-circle: 50vw;
    --max-circle: 38rem;
}


#bkg-circle {
    width: var(--grow-circle);
    height: var(--grow-circle);
    max-width: var(--max-circle);
    max-height: var(--max-circle);
    background: rgba(233, 157, 193, 0.671);
    -moz-border-radius: 50%;
    -webkit-border-radius: 50%;
    border-radius: 50%;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    font-size: 1.1rem;
    word-wrap: normal;
    padding: 1rem;
}

#bkg-circle p {
    width: 26rem;
    margin: none;
    padding: none;
}

@media screen and (max-width: 1400px),
        screen and (max-height: 800px) {
    #bkg-circle {
        border-radius: 1rem;
        margin: 1rem;
        height: 100%;
        width: 90%;
        padding: 2rem;
    }  
    
    #bkg-circle p {
        width: 100%;
        height: fit-content;
    }
}

@media screen and (max-width: 370px) {
    #bkg-circle p {
        font-size: 0.8rem;
    }
}
